* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.search-form {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: row;
  margin: 0 auto;
  padding-bottom: 40px;
  opacity: 1;
  background-color: rgb(11, 18, 149);
}

.gallery {
  width: 100%;
  display: flex;
  flex-wrap: wrap; 
  margin: -10px;
}

.info{
  display: flex;
  justify-content: space-between;
  align-items: center; 
}

.is-hidden {
display: none;
}

.load {
  display: flex;
  justify-content: center;
}

.load-more {
  margin: 30px auto;
}

.photo-card {
  border: 1px solid black;
  margin: 10px;
  overflow: hidden;
}